/* global Office */
import { AttachmentAndClassification, updateSignature } from "../utils/htmlSignature";
import { getClassification } from "../utils/getClassification";
import { getAttachments } from "../utils/readAttachments";

let attachmentsCache: AttachmentAndClassification[] = [];

const onMessageAttachmentsChangedHandler = async (event) => {
  console.log("event", event);
  console.log("onMessageAttachmentsChangedHandler--->");
  // TODO: Handle multiple additions, optimize cache and use event instead of recalculate the attachments, take into account that know it es needed to manually remove
  try {
    const attachmentsList = (await getAttachments()) || [];
    const attachmentsNotInCache = attachmentsList.filter((attachment) => {
      const findedAttachmentInCache = attachmentsCache.find((attachmentInCache) => {
        attachmentInCache.id === attachment.id;
      });
      return !findedAttachmentInCache;
    });

    const attachments = await Promise.all(
      attachmentsNotInCache.map(async (attachment) => ({
        ...attachment,
        classification: (await getClassification(attachment)).classificationLabel,
      }))
    );
    attachmentsCache = [...attachmentsCache, ...attachments]; //Update Cache
    await updateSignature(attachments);
    event.completed({ allowEvent: true });
  } catch (err) {
    console.log("Error---onMessageAttachmentsChangedHandler--->", err);
    event.completed({ allowEvent: true });
  }
};
Office.actions.associate("onMessageAttachmentsChangedHandler", onMessageAttachmentsChangedHandler);
